.item{
    color: #000;
    box-sizing: border-box;
    padding: 10px;
}
.item:not(:last-child){
    border-bottom: 1px solid rgba(0,0,0,0.11);
}

.item img{
    width: 80px;
    border-radius: 50%;
}

.likes{
    font-size: 20px;
    opacity: 0.25;
}
.likes img{
    height: 20px;
    width: 20px;
}

.item h3{
    padding: 0;
    margin: 10px 0;
}